import React from 'react';
import MaterialTable from 'material-table';
import { makeStyles } from '@material-ui/core';
import _ from 'lodash';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const PrepaidcardRecord = ({ prepaidcardRecords }) => {
  const { t } = useTranslation();

  const columns = [
    {
      title: t('Branch'),
      field: 'branch',
      render: (rowData) => `${rowData.branch.name}`
    },
    {
      title: t('Time'),
      field: 'created_at'
    },
    {
      title: t('Type'),
      field: 'type',
      render: (rowData) => t(`PrepaidcardsType.${rowData.type}`)
    },
    {
      title: t('Topup'),
      field: 'topup'
    },
    {
      title: t('Payment'),
      field: 'payment'
    }
  ];

  return (
    <MaterialTable
      title={t('Prepaidcard Records')}
      options={{
        pageSize: 10,
        pageSizeOptions: [10, 15, 30]
      }}
      columns={columns}
      data={prepaidcardRecords}
    />
  );
};

PrepaidcardRecord.prototype = {
  orderRecords: PropTypes.array.isRequired
};

export default PrepaidcardRecord;
