import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Avatar,
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  Typography,
  makeStyles,
  Grid
} from '@material-ui/core';
import _ from 'lodash';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  root: {},
  name: {
    marginTop: theme.spacing(1)
  },
  avatar: {
    height: 100,
    width: 100
  }
}));

export default function MemberInformation({ member }) {
  const classes = useStyles();
  const { t } = useTranslation();

  if (!member) {
    return null;
  }

  return (
    <Grid className={clsx(classes.root)} container spacing={3}>
      <Grid item lg={12} md={12} xl={12} xs={12}>
        <Card className={clsx(classes.root)}>
          <CardContent>
            <Box display="flex" alignItems="center" flexDirection="column" textAlign="center">
              <Avatar className={classes.avatar} />
              <Typography className={classes.name} gutterBottom variant="h3" color="textPrimary">
                {`${member.first_name}`} {`${member.last_name}`}
              </Typography>
              <Typography className={classes.name} gutterBottom variant="body1" color="textPrimary">
                {t('Phone')}: {`${member.phone}`}
              </Typography>
              <Typography className={classes.name} gutterBottom variant="body1" color="textPrimary">
                {t('Rank')}: {`${member.rank.name}`}
              </Typography>
              <Typography className={classes.name} gutterBottom variant="body1" color="textPrimary">
                {t('Register Branch')}:{' '}
                {member.register_branch
                  ? `${member.register_branch.name} (${member.register_branch.code})`
                  : t('Non Register Branch')}
              </Typography>
              <Typography className={classes.name} gutterBottom variant="body1" color="textPrimary">
                {t('Balance')}: {`${member.prepaidcard ? member.prepaidcard.balance : 0}`}
              </Typography>
              {member.email && (
                <Typography
                  className={classes.name}
                  gutterBottom
                  variant="body1"
                  color="textPrimary"
                >
                  {t('Email')}: {`${member.email}`}
                </Typography>
              )}
              {member.address && (
                <Typography
                  className={classes.name}
                  gutterBottom
                  variant="body1"
                  color="textPrimary"
                >
                  {t('Address')}: {`${member.address}`}
                </Typography>
              )}
              {member.remark && (
                <Typography
                  className={classes.name}
                  gutterBottom
                  variant="body1"
                  color="textPrimary"
                >
                  {t('Remark')}: {`${member.remark}`}
                </Typography>
              )}
            </Box>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
}

MemberInformation.prototype = {
  member: PropTypes.object.isRequired
};
