import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import {
  Container,
  Box,
  IconButton,
  SvgIcon,
  makeStyles
} from '@material-ui/core';
// import Results from './Results';
import { useParams } from 'react-router-dom';
import _ from 'lodash';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const OrderRecord = ({ orderRecords }) => {
  const classes = useStyles();
  const { t } = useTranslation();

  const columns = [
    {
      title: t('Branch'),
      field: 'branch',
      render: (rowData) => `${rowData.branch.name}`
    },
    {
      title: t('Order Id'),
      field: 'order_id'
    },
    {
      title: t('Payment Type'),
      field: 'payment_type',
      render: (rowData) => t(`PaymentType.${rowData.payment_type}`)
    },
    {
      title: t('Clerk'),
      field: 'clerk'
    },
    {
      title: t('Items Count'),
      field: 'items_count'
    },
    {
      title: t('Amount'),
      field: 'amount'
    },
    {
      title: t('Transaction time'),
      field: 'transaction_time'
    },
    {
      title: t('Status'),
      field: 'status',
      render: (rowData) => t(`TransactionStatus.${rowData.status}`)
    }
  ];

  return (
    <MaterialTable
      title={t('Order Records')}
      columns={columns}
      data={orderRecords}
      options={{
        pageSize: 10,
        pageSizeOptions: [10, 15, 30]
      }}
    />
  );
};

OrderRecord.prototype = {
  orderRecords: PropTypes.array.isRequired
};

export default OrderRecord;
