import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import {
  Container,
  Box,
  IconButton,
  SvgIcon,
  makeStyles,
  Divider,
  Tab,
  Tabs
} from '@material-ui/core';
import MemberInformation from './MemberInformation';
import ChopDetail from './ChopDetail';
import OrderRecord from './OrderRecord';
import ChopRecord from './ChopRecord';
import PrepaidcardRecord from './PrepaidcardRecord';
import Header from './Header';
import { useParams } from 'react-router-dom';
import _ from 'lodash';
import { useTranslation } from 'react-i18next';
import memberService from '../../../services/memberService';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import useMember from '../../../hooks/useMember';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function MemberChopDetail() {
  const { id } = useParams();
  const classes = useStyles();
  const { t } = useTranslation();
  const [currentTab, setCurrentTab] = useState('member_information');
  const tabs = [
    { value: 'member_information', label: 'Member Information' },
    { value: 'chops', label: 'Chops Detail' },
    { value: 'order_records', label: 'Order Records' },
    { value: 'chop_records', label: 'Chop Records' },
    { value: 'prepaidcard_records', label: 'Prepaidcard Records' }
  ];
  const isMountedRef = useIsMountedRef();
  const [member, setMember] = React.useState();

  const getMember = useCallback(async () => {
    const data = await memberService.detail(id);
    setMember(data.data.data);
  }, [isMountedRef]);

  const handleTabsChange = (event, value) => {
    setCurrentTab(value);
  };

  useEffect(() => {
    getMember();
  }, [getMember]);

  if (!member) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Member Detail')}>
      <Container maxWidth={false}>
        <Header />
        <Box mt={3}>
          <Tabs
            onChange={handleTabsChange}
            scrollButtons="auto"
            value={currentTab}
            variant="scrollable"
            textColor="secondary"
            className={classes.tabs}
          >
            {tabs.map((tab) => (
              <Tab key={tab.value} label={t(tab.label)} value={tab.value} />
            ))}
          </Tabs>
        </Box>
        <Divider />
        <Box mt={3}>
          {currentTab === 'member_information' && <MemberInformation member={member} />}
          {currentTab === 'chops' && <ChopDetail chops={member.chops} />}
          {currentTab === 'order_records' && <OrderRecord orderRecords={member.order_records} />}
          {currentTab === 'chop_records' && <ChopRecord chopRecords={member.chop_records} />}
          {currentTab === 'prepaidcard_records' && (
            <PrepaidcardRecord prepaidcardRecords={member.prepaidcard_records} />
          )}
        </Box>
      </Container>
    </Page>
  );
}
