import React from 'react';
import MaterialTable from 'material-table';
import { makeStyles } from '@material-ui/core';
import _ from 'lodash';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const ChopRecord = ({ chopRecords }) => {
  const { t } = useTranslation();

  const columns = [
    {
      title: t('Branch'),
      field: 'branch',
      render: (rowData) => `${rowData.branch.name}`
    },
    {
      title: t('Time'),
      field: 'created_at'
    },
    {
      title: t('Type'),
      field: 'type',
      render: (rowData) => t(`ChopsType.${rowData.type}`)
    },
    {
      title: t('Earn Chops'),
      field: 'chops'
    },
    {
      title: t('Consume Chops'),
      field: 'consume_chops'
    }
  ];

  return (
    <MaterialTable
      title={t('Chop Records')}
      options={{
        pageSize: 10,
        pageSizeOptions: [10, 15, 30]
      }}
      columns={columns}
      data={chopRecords}
    />
  );
};

ChopRecord.prototype = {
  orderRecords: PropTypes.array.isRequired
};

export default ChopRecord;
