import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import useMember from '../../../hooks/useMember';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Edit as EditIcon, Trash } from 'react-feather';
import Header from './Header';
// import Results from './Results';
import { useParams } from 'react-router-dom';
import _ from 'lodash';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function MemberChopDetail({ chops }) {
  const classes = useStyles();
  const { t } = useTranslation();

  if (!chops) {
    return null;
  }

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>{t('Branch')}</TableCell>
            <TableCell>{t('Branch Id')}</TableCell>
            <TableCell>{t('Chops')}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {chops.map((row) => (
            <TableRow key={row.id}>
              <TableCell component="th" scope="row">
                {row.branch.name}
              </TableCell>
              <TableCell>{row.branch.code}</TableCell>
              <TableCell>{row.chops}</TableCell>
            </TableRow>
          ))}
          <TableRow key={'TOTAL_CHOPS'}>
            <TableCell colSpan={2}>{t('Total')}</TableCell>
            <TableCell>{_.sumBy(chops, 'chops')}</TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
}

MemberChopDetail.prototype = {
  chops: PropTypes.array.isRequired
};
